body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.signup-container {
  margin: auto;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
}

h2 {
  font-size: 4rem;
  font-weight: bold;
  color: #333;
  text-align: center;
}

p {
  color: #666;
  margin-top: -35px;
  text-align: left;
}

label {
  text-align: left;
}

a {
  color: purple;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 400px;
  margin-top: 50px;
}

input {
  padding: 10px;
  border: 2px solid purple;
  border-radius: 20px;
  font-size: 1rem;
}

button {
  background: purple;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 1rem;
  margin-top: 10px;
}

.remember-me {
  display: flex;
  align-items: center;
  gap: 5px;
}

.forgot-password {
  display: block;
  margin-bottom: 15px;
  font-size: 14px;
}